import React, { useState } from "react"
import "./App.css"
import fonts from "./fonts.json"
import FontSample from "./FontSample"

const App = () => {
  const [query, setQuery] = useState("")
  const [text, setText] = useState("")

  const queryHandler = event => {
    setQuery(event.target.value)
  }

  const textHandler = event => {
    setText(event.target.value)
  }

  const currQuery = query.split(" ")
  let filteredFonts = []
  for (let i = 0; i < fonts.length; i++) {
    const lowercaseFont = fonts[i].toLowerCase()
    let check = true
    for (let j = 0; j < currQuery.length; j++) {
      if (lowercaseFont.indexOf(currQuery[j].toLowerCase())) {
        check = false
        break
      }
    }
    if (check) filteredFonts.push(fonts[i])
  }

  return (
    <div className="App">
      <div className="container">
        <div className="header">
          <h1>Fonts Galore</h1>
          <h5>
            Preview all your open source fonts, in one place! See a font you
            like? Simply
          </h5>

          <code>npm install --save typeface-( font name )</code>
          <h5>Then in your app or site's entry file</h5>
          <code>require("typeface-open-sans")</code>
          <h5>
            Thanks to Kyle Matthews for compiling the fonts{" "}
            <a href="https://github.com/KyleAMathews/typefaces">here</a> and
            putting them on npm.
          </h5>
        </div>
        <div className="inputs">
          <div className="input">
            <label>Text:</label>
            <input
              type="text"
              onChange={textHandler}
              placeholder="Type text here!"
            />
          </div>
          <div className="search">
            <label>Font Search:</label>
            <input
              type="text"
              onChange={queryHandler}
              placeholder="Search fonts!"
            />
          </div>
        </div>

        <div className="font-sample-container">
          {filteredFonts.map(font => (
            <a href={`/${font}`}>
              <FontSample key={font} font={font} text={text} />
            </a>
          ))}
        </div>
      </div>
    </div>
  )
}

export default App
