import React from 'react';
import './FontSample.css';

const FontSample = ({font, text, onClick}) => {
  const useFont = font || 'Open Sans';
  const useText = text || 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz01234567890;/.,?":';

  const clickHandler = () => {
    onClick(useFont);
  };

  return (
    <div onClick={clickHandler} className="font-sample" style={{fontFamily: useFont}}>
      <div className="label"> {useFont} </div>
      <div>
        <p>{useText}</p>
      </div>
    </div>
  );
};

export default FontSample;
